.team-details {
    padding: 0 80px;
    background-color: #7986cb;
    color: #ffffff;
    header {
        padding: 0;
        margin-bottom: 20px;
        img {
            width: 210px;
            margin: 20px 20px 20px 0;
        }
        h1 {
            font-size: rem(100px);
        }
    }
    h2 {
        font-size: rem(24px);
        margin-bottom: 10px;
    }
    .content {
        margin-bottom: 10px;
        p {
            margin-right: 10px;
        }
        a {
            color: #ffffff;
            text-decoration: underline;
        }
    }
    table {
        border-collapse: collapse;
        border: 1px solid #ffffff;
        width: 500px;
        table-layout: fixed;
        th,
        td {
            padding: 5px;
            border: 1px solid #ffffff;
            text-align: center;
            vertical-align: middle;
        }
    }
}